import React, { Component } from 'react';
import ReactDOM from 'react-dom';
//import { Button, Menu, Icon} from 'antd';
import { CSSTransition,TransitionGroup} from 'react-transition-group';
import { createStore } from 'redux';
import LazyLoad from 'react-lazyload';

import './css/reset.css';
import './css/index.scss';
import './font/iconfont.css';
import registerServiceWorker from './registerServiceWorker';


const reducer = (state={isNavCur:false},action)=> {
	switch(action.type) {
		case 'toggleNav':
			return {...state,isNavCur:(state.isNavCur?false:true)};
		default:
			return state;
	}
}
const store = createStore(reducer);
const viewPortH = document.documentElement.clientHeight;

class App extends Component {
	handleNavToggle = () => {
  		store.dispatch({type:'toggleNav'});
  	}
  	render() {
    	return (
			<section>
				<NavBar />
				<MainScreen handleNavToggle = {this.handleNavToggle} />
				<LazyLoad  height={600} offset={[-viewPortH/3, 0]}>
					<Skills />
				</LazyLoad>
				<PersonalInfo />
				<Contacts />
			</section>
    	)
  	}
}

class NavBar extends Component { 
	componentWillMount() {
		this.unsubscribe = store.subscribe(() => {
			this.forceUpdate();
		})
	}
    render() {
    	console.log('toggleNavBar');
		return (
			<section className={store.getState().isNavCur?'nav-container cur':'nav-container'}>
				<ul className="nav-list">
					<li>
						<a href="intro">扉页
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">技能
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">作品
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">关于我
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">联系方式
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
				</ul>
			</section>
		)
	}
	componentWillUnmount() {
		this.unsubscribe();
	}
}

function MainScreen ({handleNavToggle}) {
	console.log('renderMainScreen');
	return (
		<section className="screen-first">
			<img src={require("./images/main/mainPic.jpg")} alt=""/>
			<div id="nav-toggle" onClick={handleNavToggle}><p>click me~</p></div>
			<TransitionGroup
				appear={true}
			>
				<CSSTransition
					key={'k-sign'}
	                in={true}
	                timeout={600}
	                classNames="signature"
	            >
			        <div className="signature">lxltmx</div>
			    </CSSTransition>
			    <CSSTransition
			    	key={'k-title'}
	                in={true}
	                timeout={1600}
	                classNames="sign-subtit"
	            >
			        <div className="sign-subtit">follow the heart</div>
			    </CSSTransition>
			</TransitionGroup>
		</section>
	)
}

